export default {
  async asyncData ({ $content, params, error }) {
    const page = await $content(params.course, params.unit, params.lesson).fetch().catch((err) => {
      error({ statusCode: 404, message: 'Page not found' })
    })
    //console.log(page)

    return {
      page, p:page.index
    }
  }
}
